import { Component, OnInit, ViewEncapsulation, AfterViewInit, ElementRef, ViewChild, HostBinding} from '@angular/core';
import {FormsModule, ReactiveFormsModule} from '@angular/forms';
import {FormControl, Validators, FormGroup, ValidationErrors} from '@angular/forms';
import {HttpClientModule} from '@angular/common/http';
import {HttpClient, HttpHeaders} from '@angular/common/http';
import { Helpers } from '../../../../../../helpers';
import { ScriptLoaderService } from '../../../../../../_services/script-loader.service';
import { FLY_IN_OUT_ANIMATION }   from '../../../../../../animations/fly-in-out.animation';
import { ROUND_ANTICLOCK_ANIMATION }   from '../../../../../../animations/round-anticlock.animation';
import { Router, Event, NavigationStart, NavigationEnd, ActivatedRoute } from '@angular/router';
import { ToastComponent } from '../../../../../layouts/toast-notification/toast.component';
import 'rxjs/add/operator/filter' ;

const EMAIL_REGEX = /^[a-zA-Z0-9.!#$%&â€™*+/=?^_`{|}~-]+@[a-zA-Z0-9-]+(?:\.[a-zA-Z0-9-]+)*$/;
const MOBILE_REGEX=/^[91]{2}-\d{10}$/;
const PIN_REGEX=/^\d{6}$/;
const NUM_REGAX=/[0-9]/;

@Component({
    selector: ".m-grid__item.m-grid__item--fluid.m-wrapper",
    templateUrl: "./customer-title-add.component.html",
    encapsulation: ViewEncapsulation.None,
	animations: [ 
    	FLY_IN_OUT_ANIMATION,
		ROUND_ANTICLOCK_ANIMATION
  ],
})

export class CustomerTitleAddComponent  {
@HostBinding('@flyInOutTrigger') flyInOutTrigger = 'in';
@ViewChild(ToastComponent) toast: ToastComponent;
	
titleForm: FormGroup;
static SerialnoMandatory ="0";
titleId = 0;  
ajaxData : CustomerTitleData[];
customertitle = new CustomerTitleData();  
config = new configDetails(); 
status;

msg = [];
bootstrapFromValues = [];

constructor(private _script: ScriptLoaderService, 	private http: HttpClient, private _router: Router, private route:ActivatedRoute) {
	this.route.queryParams.filter(params => params.titleId).subscribe(params=>{
			if(params.titleId != undefined){
				this.titleId=params.titleId; 
			}
		}); 
	if(this.titleId == 0){
		this.status = "Add";
	}else{
		this.status = "Update";
		this.populateData();
	}
	}
	
    ngOnInit() {
		this.titleForm = new FormGroup({			
			'titleId' : new FormControl(''),
			'titleDesc' : new FormControl('', [ Validators.required]),
			'titleEntryId' : new FormControl(''),
			'titleEntryDate' : new FormControl(''),
			'titleModifiedId' : new FormControl(''),
			'titleModifiedDate' : new FormControl(''),
			
		});
	}
	  
	    get titleDesc(){ return this.titleForm.get('titleDesc')};
	    get titleEntryId(){ return this.titleForm.get('titleEntryId')};
	    get titleEntryDate(){ return this.titleForm.get('titleEntryDate')};
	    get titleModifiedId(){ return this.titleForm.get('titleModifiedId')};
	    get titleModifiedDate(){ return this.titleForm.get('titleModifiedDate')};
	 
	 
	 populateData(){
			  	const headers = new HttpHeaders({'Content-Type':'application/json'});
			  this.http.post<CustomerTitleData>(sessionStorage.getItem('requestUrl')+'/axelacrm/customer/title-data',
							 			JSON.stringify({ titleId :this.titleId}),
									{ headers, withCredentials: true }).subscribe(data =>{
				  														this.customertitle = data['populateData'][0];
																		console.log(this.customertitle);
			  															},
															 (err) => {
																	/* this function is executed when there's an ERROR */
																	console.log("ERROR: "+err);
															 });
	 }
 
	   ngAfterViewInit(){ }	
	
 
		 toggleAnimation(){
			this._router.navigate(['portal/customer-title-list']);
		 }

 
	  	onSubmit() {
			this.bootstrapFromValues = $('#m_form_1').serializeArray();
			for(var i=0;i<this.bootstrapFromValues.length;i++){
				this.customertitle[this.bootstrapFromValues[i].name]=this.bootstrapFromValues[i].value;
			}
			if(!this.titleForm.invalid){
				  const headers = new HttpHeaders({'Content-Type':'application/json'});
			if(this.titleId == 0){ 
				this.customertitle['requestType'] = 'add';
			}else{
				this.customertitle['requestType'] = 'update';
				this.customertitle['titleId'] = this.titleId+'';
			}
			 this.http.post(sessionStorage.getItem('requestUrl')+'/axelacrm/customer/title',
									JSON.stringify(this.customertitle),
						{ headers, withCredentials: true }).subscribe(data => {
														if(data['successmsg'].includes('Successfully')){
															this.msg[0]='success';
															this.msg[1]=data['successmsg'];
														}else{
															this.msg[0]='Error';
															this.msg[1]=data['errormsg'];
														}
															this.toast.showtoast(this.msg);
															this.toggleAnimation();
															this.titleForm.reset();
														},
														err =>{
			 												this.msg[0]='Error';
			 												this.msg[1]=err['error'];
			 												this.toast.showtoast(this.msg);
		 												});
		  } 
			console.log(this.customertitle);
	  }
 
  onDelete(){
		 this.customertitle['requestType'] = 'delete';
		this.customertitle['titleId'] = this.titleId+'';
		 const headers = new HttpHeaders({'Content-Type':'application/json'});
		 this.http.post(sessionStorage.getItem('requestUrl')+'/axelacrm/customer/title',
									JSON.stringify(this.customertitle),
						{ headers, withCredentials: true }).subscribe(data => {			 											
			 											if(data['successmsg'].includes('Successfully')){
															this.msg[0]='success';
															this.msg[1]=data['successmsg'];
														}else{
															this.msg[0]='Error';
															this.msg[1]=data['successmsg'];
														}
			 											this.toast.showtoast(this.msg);
			 											this.toggleAnimation();
			 											this.titleForm.reset();
//			 											this.list.getData();
														},
														err =>{
			 												console.log(err['error']['desc']);
			 												this.msg[0]='Error';
			 												this.msg[1]=err['error'];
			 												this.toast.showtoast(this.msg);
		 												});
	 }
	  	
	
}

export class CustomerTitleData {
	
	titleId: string ;
	titleDesc: string;
	titleEntryId:string;
	titleEntryBy:string;
	titleEntryDate:string;
    titleModifiedId:string;
	titleModifiedBy:string;
    titleModifiedDate:string;
	
	 
}
export class configDetails {
	configCustomerSob:string;
	configCustomerSoe:string;
	configCustomerAddress:string;
}



